	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"In this talk will discuss the circumstances that led to Erlang, the design choices that were made, what the future holds for Erlang and how it all relates  to building scalable distributed systems now.\nhttp://www.erlang-factory.com/berlin2014/robert-virding\n\nAbout Robert\n\nRobert Virding is Principal Language Expert at Erlang Solutions Ltd. While at Ericsson AB, Robert was one of the original members of the Ericsson Computer Science Lab, and co-inventor of the Erlang language. He took part in the original system design and contributed much of the original libraries, as well as to the current compiler.\n\nWhile at the lab he also did a lot of work on the implementation of logic and functional languages and on garbage collection. He has also worked as an entrepreneur and was one of the co-founders of one of the first Erlang startups (Bluetail). Robert also worked a number of years at the Swedish Defence Materiel Administration (FMV) Modelling and Simulations Group. He co-authored the first book (Prentice-Hall) on Erlang, and is regularly invited to teach and present throughout the world.","meta":{"duration":"PT37M24S"},"presenter":{"name":"Robert Virding"},"source":{"id":"od6CfA8xEcM","provider":"youtube"},"slug":"erlang-evolution","tag":["Erlang Factory Lite","Erlang"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/od6CfA8xEcM/default.jpg","height":90,"width":120}},"title":"Erlang: Evolution"}}]}},"pathContext":{"slug":"erlang-factory-lite","tag":"Erlang Factory Lite"}}

